@import url(https://fonts.googleapis.com/css2?family=Fondamento:ital@0;1&family=Homemade+Apple&display=swap);
body{
  text-align: center;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

nav {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  flex-basis: 30%;
}

#project-count {
  text-align: center;
}

h1 {
  text-align: left;
  font-family: "Fondamento", serif;
}

.project {
  margin: 0 auto;
  border: 1px solid black;
  border-radius: 8px;
  width: 500px;
}
span > * {
  display: inline;
}

span > input {
  width: 50px;
}

button, textarea {
  background-color: white;
}

input, button, span, textarea, #name-input{
  font-family: "Homemade Apple", cursive;
  font-size: 24px;
  height: 50px;
  border: none;
  text-align: center;
  text-indent: 3px;
}

textarea {
  padding: 10px;
  margin: 5px;
}

p, button, label {
  font-family: Arial, Helvetica, sans-serif;
  text-transform: uppercase;
  font-size: 10px;
  letter-spacing: 2px;
}

label {
  display: block;
}

input:focus, textarea {
  outline: #74b863;
  border-radius: 8px;
  width: 90%;
  outline: none;
  background-color: #f7f8f9;
}

.reset {
  font-family: Arial, Helvetica, sans-serif;
  text-transform: uppercase;
  font-size: 10px;
  letter-spacing: 2px;
  height: 10px;
}

span.counter {
  height: 30px;
  width: 90px;
}

.notes {
  height: 100px;
  font-family: Arial, Helvetica, sans-serif;
  text-transform: uppercase;
  font-size: 10px;
  letter-spacing: 2px;
}

#project-listing {
  display: flex;
  /* flex-direction: row-reverse; */
  flex-wrap: nowrap;
  overflow-x: auto;
  margin: 60px auto ;
}

section {
  flex: 0 0 auto;
  border: 1px solid black;
  border-radius: 5px;
  width: 400px;
  margin-right: 60px;
}

#txt {
  margin: 10px;
}

#hide {
  position: absolute;
  height: 0;
  overflow: hidden;
  white-space: pre;
}


